@import url(https://fonts.googleapis.com/css?family=Nunito+Sans:300,400,600,800);
body{font-size:16px;color:#fff;background-color:#282c34}a{color:#61dafb;outline:none}h1{font-size:2.25em;font-weight:800;font-style:normal;text-align:center;letter-spacing:-1px}h1:first-child{margin-top:0}
.App{font-size:1em;font-family:"Nunito Sans",helvetica,arial,sans-serif;font-weight:600;text-align:center;margin:0em auto 2em;max-width:48em;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale}header{border-bottom:1px solid rgba(255,255,255,.1);margin-bottom:1em}.content{font-size:1.15em;line-height:1.5em;padding:1.5em 1.75em 0}article{text-align:left}@media screen and (max-width: 520px){.App{font-size:.85em}}
.TheNavigation_navigation__20uHv{margin:2em auto 2.5em;padding:0 1.25em;display:inline-block;vertical-align:middle;background:transparent;white-space:nowrap;border:3px solid #fff;border-radius:2.5em}.TheNavigation_navigation__20uHv a{color:#fff;font-size:1.1em;font-weight:800;text-decoration:none;cursor:pointer;padding:.575em .65em;display:inline-block}.TheNavigation_navigation__20uHv a.TheNavigation_active__1bv2q{color:#61dafb}

.search{width:100%;display:block;padding-top:.5em;padding-bottom:.5em;padding-left:2.95em;padding-right:.75em;color:#333;font-size:1.5em;font-weight:700;border:none;border-radius:.5em;background:#fff;box-sizing:border-box;outline:none;-webkit-appearance:none;-moz-appearance:none;appearance:none;background-image:url(/static/media/search.7fdcc1a9.svg);background-repeat:no-repeat;background-position-y:50%;background-position-x:1em;background-color:#fff;background-size:1em}@media screen and (max-width: 520px){.search{font-size:1.15em}}
.tunes-list{margin:3em 0 0;padding-left:0;list-style:none}.tunes-list li{margin-bottom:3em}.tunes-list li:last-child article{margin-bottom:0}
.song{color:#262626;text-align:left;max-width:24em;margin:0 auto 2em;position:relative;box-sizing:border-box;background-color:#fff;border-radius:.75em;border-bottom-left-radius:.9em;border-bottom-right-radius:.9em;border:1px solid #000}.song .inside{padding:1.35em 1.75em 1.75em}.song h2{font-size:inherit;line-height:1.5em;margin:0}.song .player{display:flex;align-items:center;flex-direction:row;justify-content:space-between;margin-top:1.5em}.song img{width:5em;border:3px solid #262626;border-radius:.5em}.song audio{outline:none;max-width:14em;background:#262626}.song audio::-webkit-media-controls-panel,.song audio::-webkit-media-controls{background:#fff}.song .meta{font-size:.75em;font-weight:800;line-height:1.5;color:rgba(255,255,255,.9);padding:1.5em 2.25em;background:rgba(0,0,0,.9);border-bottom-left-radius:1em;border-bottom-right-radius:1em}@media screen and (max-width: 520px){.player{flex-direction:column}}.song-enter-active,.song-exit-active{transition:all .2s}.song-enter{opacity:0;transform:scale(0.85)}.song-enter-active{opacity:1;transform:translateX(0)}.song-exit{opacity:1}.song-exit-active{opacity:0;transform:scale(0.85)}

